import React, { Component } from 'react';
import cx from 'classnames'; 
import Button from './scrollButton';
import { Link } from 'react-router-dom';
  
export default class Header extends Component {

  state = {
    activeModal: false,
  }

  onClick = e => {
    const { tagName } = e.target;
    if (tagName !== 'LI' && tagName !== 'IMG') return;
    const { activeModal } = this.state;
    this.setState({
      activeModal: !activeModal
    })
  }

  render () {
    const { activeModal } = this.state;
    
    const modalAuthClassName = cx({
      'modal-auth': true,
      'modal-auth--active': activeModal
    })

    const chevronClassName = cx({
      settings__chevron: true,
      'active': activeModal
    })

    return (
      <header className="header">
        <div className="container">
          <ul className="settings">
            <li 
              className="settings__item"
              onClick={this.onClick}
            >
            <div className={modalAuthClassName}>
              <input type="text" placeholder=""/>
              <input type="text" placeholder=""/>
              <div className="modal-auth__actions">
                <button className="modal-auth__button"></button>
                <button className="modal-auth__button"></button>
              </div>
            </div>
              <img className="settings__user" src="../assets/images/user@1X.png" alt="user" />
               
              <img
                className={chevronClassName} 
                src="../assets/images/ 1  2@1X.png" 
                alt="chevron"
              />
            </li>
            <li className="settings__item">
              <a href="tel: +78005006105">8 800 500-61-05</a>
              <p> </p>
            </li>
          </ul>
          <nav>
            <ul className="menu">
              <li className="menu__item">
                <Link to='/' className="menu__item-link"></Link>
              </li>
              <li className="menu__item">
                <Link to='/about' className="menu__item-link"> </Link>
              </li>
              <li className="menu__item">
                <a className="menu__item-link" href="#"></a>
              </li>
              <li className="menu__item">
                <a className="menu__item-link" href="#"></a>
              </li>
              <li className="menu__item">
                <a className="menu__item-link" href="#"> </a>
              </li>
            </ul>
          </nav>
          <div className="header__logo">
            <img src="../assets/images/logo@1X.png" alt="logo" />
          </div>
          <div>
            <p className="header__slogan"> </p>
            <p className="header__slogan--large">  </p>
          </div>
          <Button />
          <div className="header__figure">
            <img src="../assets/images/ 1@1X (1).png" alt="figure" />
          </div>
        </div>
      </header>
    )
  }
}